import Vue from 'vue'
import Vuex from 'vuex'
import AdminSchedulesStore from './modules/AdminSchedulesStore'
import BarberStore from './modules/BarberStore'
import BarbershopStore from './modules/BarbershopStore'
import DashboardStore from './modules/DashboardStore'
import SnackbarStore from './modules/SnackbarStore'
import LoaderStore from './modules/LoaderStore'
import MenuStore from './modules/MenuStore'
import NotificationStore from './modules/NotificationStore'
import TermPromptStore from './modules/TermPromptStore'
import UserStore from './modules/UserStore'

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    AdminSchedulesStore,
    BarberStore,
    BarbershopStore,
    DashboardStore,
    LoaderStore,
    MenuStore,
    NotificationStore,
    SnackbarStore,
    TermPromptStore,
    UserStore,
  },
});
